@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500;700);
.right-side{
  .subtitle{
    text-align: center;
    font-size: 4rem;
    color: #68686b;
    margin: 0;
  }

  .caption{
    text-align: center;
    font-size: 1.5rem;
    margin: 0;
    margin-bottom: 2rem;
    color: #b4b3b3;
  }
}

.searcher-wp{
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.searcher-input{
  background-color: #f4f2f2;
  border: none;
  color: #646464;
  padding: 0.7rem 1rem;
  border-radius: 30px;
  max-width: 25rem;
  min-width: 18rem;
  transition: all ease-in-out .5s;

  &:hover, &:focus {
    box-shadow: 0 0 1em #00000013;
  }

  &:focus{
    outline: none;
    background-color: #f0eeee;
  }
}

ul{
  padding: 0;
}

.list-item{
  list-style: none;
  border-radius: 8px;
  background: #EAEBF3;
  box-shadow:  5px 5px 10px #d7d8e0,
              -5px -5px 10px #fdfeff;
  margin: 1rem 0;
  display: grid;
  grid-template-columns: 4rem 1fr 4rem;
  place-items: center;
  border-right: 8px solid #EAEBF3;
  max-width: 25rem;
  min-width: 18rem;
  
  & .done-btn,
  & .delete-btn{
    width: 2rem;
    height: 2rem;
    border-radius: 38px;
    background: #EAEBF3;
    box-shadow:  6px 6px 12px #d7d8e0,
                 -6px -6px 12px #fdfeff;
    border: none;
    transition: all .2s;

    & svg{
      color: #9b9ba1;
    }

    &:hover{
      border-radius: 50px;
      background: linear-gradient(145deg, #fafbff, #d3d4db);
      box-shadow:  5px 5px 10px #c7c8cf,
                   -5px -5px 10px #ffffff;
      cursor: pointer;
    }

    &:active{
      border-radius: 50px;
      background: #EAEBF3;
      box-shadow: inset 5px 5px 10px #c7c8cf,
                  inset -5px -5px 10px #ffffff;
    }
  }

  &.check{
    box-shadow:  5px 5px 10px #dcdde4,
                 -5px -5px 10px #f8f9ff;
    border-right-color: #80d483;
  }

  &.pendding{
    border-right-color: #80b0d4;
  }
}

.modal-component{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: grid;
  place-items: center;

  & .card{
    width: 550px;
    height: 550px;
    border-radius: 8px;
    background-color: #EAEBF3;
    position: relative;

    & .close-btn{
      position: absolute;
      top: 1rem;
      right: 1rem;
      border: none;
      border-radius: 50%;
      background: #EAEBF3;
      box-shadow:  8px 8px 16px #dad9d9,
                    -8px -8px 16px #f8f9ff;
      
    }
  }
}

.new-task{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2rem;
  
  .subtitle{
    font-size: 2rem;
    margin-bottom: 3rem;
    color: #68686b;
  }

  .form-group{
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;

    .label{
      margin-bottom: .5rem;
      color: #a4a4aa;
    }

    & input{
      background-color: #f4f2f2;
      border: none;
      color: #646464;
      padding: 0.7rem 1rem;
      border-radius: 30px;
      max-width: 25rem;
      min-width: 18rem;
      transition: all ease-in-out .5s;
    }

    & input:hover, & input:focus {
      box-shadow: 0 0 1em #00000013;
    }

    & input:focus{
      outline: none;
      background-color: #f0eeee;
    }
  }

  & button.submit-btn{
    width: 12rem;
    border: none;
    height: 2rem;
    border-radius: 16px;
    background: #EAEBF3;
    box-shadow:  8px 8px 16px #dad9d9,
                 -8px -8px 16px #f8f9ff;

    &:hover{
      border-radius: 50px;
      background: linear-gradient(145deg, #eaeaea, #EAEBF3);
      box-shadow:  5px 5px 10px #c7c8cf,
                    -5px -5px 10px #ffffff;
      cursor: pointer;
    }

    &:active{
      border-radius: 50px;
      background: #EAEBF3;
      box-shadow: inset 5px 5px 10px #c7c8cf,
                  inset -5px -5px 10px #ffffff;
    }
  }

  & .copyrght-btn{
    position: absolute;
    right: 2rem;
    bottom: 2rem;
    width: 2rem;
    height: 2rem;
    display: grid;
    place-items: center;
    border: none;
    border-radius: 50%;
    background: #EAEBF3;
    box-shadow:  8px 8px 16px #dad9d9,
                  -8px -8px 16px #f8f9ff;

    &:hover{
      border-radius: 50px;
      background: linear-gradient(145deg, #eaeaea, #EAEBF3);
      box-shadow:  5px 5px 10px #c7c8cf,
                    -5px -5px 10px #ffffff;
      cursor: pointer;
    }

    &:active{
      border-radius: 50px;
      background: #EAEBF3;
      box-shadow: inset 5px 5px 10px #c7c8cf,
                  inset -5px -5px 10px #ffffff;
    }
  }

  &:hover{
    cursor: pointer;
  }
}

* {
  font-family: 'Quicksand', sans-serif;
}

body{
  margin: 0;
  padding: 0;
  background-color: #EAEBF3;
}

#root{
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.card-app{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 16px;
  background: #EAEBF3;
  box-shadow:  12px 12px 24px #dcdde4,
              -12px -12px 24px #f8f9ff;
}

.divisor-container{
  width: 100%;
  display: grid;
  place-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;

  & hr{
    width: 15rem;
    height: .5rem;
    border: none;
    background-color: #dcdde4;
    border-radius: 6px;
  }
}

@media(max-width: 640px){
  .card-app{
    box-shadow: none;
    width: 100vw;
  }
}

